<template>
  <div class="passreset__screen">
    <v-card flat class="resetpass pa-10 mx-5" max-width="400px" elevation="12">
      <img class="forgetpass__image" src="@/assets/resetpass.svg" alt="" />
      <v-card-text>
        <v-text-field
          solo
          rounded
          outlined
          dense
          label="E-mail"
          v-model.trim="email"
        />
        <v-sheet dark v-if="error" class="red ma-2 pa-2">{{ error }}</v-sheet>
        <v-btn
          outlined
          class="success white--text mx-auto"
          @click="passwordReset"
          block
          >Send Confirmation Email</v-btn
        >

        <v-btn block outlined dark to="/login" class="mt-2"
          >Go to login page.
        </v-btn>
      </v-card-text>
    </v-card>
  </div>
</template>

<script>
import { auth } from "@/plugins/firebase";
export default {
  data() {
    return {
      email: "",
      error: "",
    };
  },
  methods: {
    passwordReset() {
      auth
        .sendPasswordResetEmail(this.email)
        .then(() => {
          this.showAlert();
          this.email = "";
        })
        .catch((error) => {
          this.error = error.message;
        });
    },
    showAlert() {
      this.$swal("Email sent!", "Check your inbox and spam.", "success");
    },
  },
};
</script>

